

























import i18n from 'i18next';
import { defineComponent, ref, watchEffect } from '@vue/composition-api';

import { materialColorPicker } from '@/views/material/form-related';

function getColorNameByLang(color: string) {
  const colorItem = materialColorPicker.find(item => item.name === color);
  if (!colorItem) return color;
  return i18n.language === 'cn' ? colorItem.cnName : colorItem.name;
}
function getColorHexByName(color: string) {
  const colorItem = materialColorPicker.find(item => item.name === color);
  if (!colorItem) return '#fff';
  return colorItem.heximal;
}

export default defineComponent({
  name: 'MaterialColorViewer',
  props: {
    // 标签名称
    label: {
      type: String,
      required: true,
    },
    // 内容
    content: {
      type: [String],
      default: '',
    },
  },
  setup(props) {
    return {
      getColorHexByName,
      getColorNameByLang,
    };
  },
});
