<template>
  <span class="unit-text">{{ unit }}</span>
</template>

<script>
import { defineComponent, ref, watch } from '@vue/composition-api';
import i18n from 'i18next';

import { getCountryList, getDictionaryByTypeCode } from '@/api/dictionary';

export default defineComponent({
  props: {
    // 单位的类型
    type: {
      type: String,
      required: true,
    },
    // 单位的值
    value: {
      type: [Number, String],
      // required: true,
    },
  },
  setup(props, { root: { $errHandler } }) {
    const unit = ref('');

    watch(() => props.value, async () => {
      // 处理空值和null情况
      if (props.value) {
        try {
          let units;
          let currentUnit;
          // 国家字典的情况
          if (props.type === 'country') {
            units = (await getCountryList()).data;
            currentUnit = units.find(u => +u.id === +props.value);
            // unit.value = currentUnit ? currentUnit.usName : '';
            unit.value = currentUnit
              ? (i18n.language === 'en' ? (currentUnit.usName || currentUnit.name) : (currentUnit.name || currentUnit.usName))
              : '';
          } else {
            units = (
              await getDictionaryByTypeCode(props.type, i18n.language)
            ).data;
            currentUnit = units.find(u => +u.itemValue === +props.value);
            unit.value = currentUnit ? currentUnit.label : '';
          }
        } catch (err) {
          $errHandler(err);
        }
      } else {
        unit.value = '';
      }
    }, {
      immediate: true,
    });

    return { unit };
  },
});
</script>

<style lang="scss" scoped>
.unit-text {
  display: inline-block;
  line-height: 22px;;
  vertical-align: middle;
  white-space: normal;
  & + .unit-text {
    margin-left: 8px;
  }
}
</style>
