





































import i18n from 'i18next';
import {
  defineComponent, ref, watch, computed,
} from '@vue/composition-api';
import CustomCollapse from '@/components/collapse/Collapse.vue';

import MaterialColorViewer from '@/views/material/info/components/ColorViewer.vue';
import FromFieldText from '../../../components/FromFieldText.vue';

export default defineComponent({
  components: {
    CustomCollapse,
    FromFieldText,
    MaterialColorViewer,
  },
  props: {
    material: {
      type: Object,
      require: true,
      default: () => ({}),
    },
  },
  setup(props) {
    const loading = ref(true);
    const fields = ref<any>([]);
    const collapseStatus = ref<Boolean>(false);

    watch(() => props.material, (val, oldVal) => {
      // 当请求回material数据才渲染页面
      loading.value = !!oldVal;

      fields.value = [
        {
          label: i18n.t('material.info.material_type'),
          units: [
            { type: 'materialType', value: val.materialType },
          ],
        },
        { label: i18n.t('material.info.supplier_article_name'), prop: 'supplierArticleName' },
        { label: i18n.t('material.info.supplier_article_number'), prop: 'supplierArticleNo' },
        { label: i18n.t('material.info.supplier_name'), prop: 'supplierName' },
        { label: i18n.t('material.info.part_used'), prop: 'partUsed' },
        {
          label: i18n.t('material.info.moq'),
          prop: 'moq',
          units: [
            { type: 'mqqUnit', value: val.moqUnit },
          ],
        },
        { label: i18n.t('material.info.season'), prop: 'season' },
        { label: i18n.t('material.info.color'), prop: 'color' },
        {
          label: i18n.t('material.info.sample_period'),
          prop: 'samplePeriod',
          units: [
            { type: 'date', value: val.samplePeriodUnit },
          ],
        },
        {
          label: i18n.t('material.info.lead_time'),
          prop: 'productionLeadTime',
          units: [
            { type: 'date', value: val.productionLeadTimeUnit },
          ],
        },
        { label: i18n.t('material.info.specification'), prop: 'specification' },
        {
          label: i18n.t('material.info.thickness'),
          prop: 'thickness',
          units: [
            { type: 'thicknessUnit', value: val.thicknessUnit },
          ],
        },
        {
          label: i18n.t('material.info.original_country'),
          units: [
            { type: 'country', value: val.materialCountryOfOrigin },
          ],
        },
      ];
    }, { deep: true });

    const printPatternsInfoTags = computed(() => {
      return props.material.printPatternsInfo?.map((tag: string|object) => {
        if (typeof tag === 'string') {
          return tag;
        }
        return (tag as any).tag;
      }).join('、') || '';
    });
    const patternsTags = computed(() => {
      return props.material.patterns?.map((tag: string|object) => {
        if (typeof tag === 'string') {
          return tag;
        }
        return (tag as any).tag;
      }).join('、') || '';
    });
    const tagsDisplay = computed(() => {
      return [printPatternsInfoTags.value, patternsTags.value].filter(e => e && e.length > 0).join('、');
    });

    return {
      collapseStatus,
      fields,
      loading,
      tagsDisplay,
    };
  },
});
